@charset "UTF-8";
/* CSS com alguns ajustes para o modelo inforce de site */
/* BASE */
* {
  outline: none !important;
}

body {
  font-family: "Montserrat";
}

a {
  color: #555;
}

a:hover {
  text-decoration: underline;
}

a.btn,
.btn {
  background: #b3953d;
  color: #FFF;
  border: 0 none;
  box-shadow: none;
}

a.btn.focus, a.btn:focus,
.btn.focus,
.btn:focus {
  background: #b3953d;
  color: #FFF;
}

a.btn:hover,
.btn:hover {
  background: #c7ac5c;
}

a.btn[disabled],
.btn[disabled] {
  background: #c7ac5c;
}

a.btn.btn-brand,
.btn.btn-brand {
  box-shadow: 1px 1px 1px rgba(0, 0, 0, 0.75);
  background: #c7ac5c;
}

a.btn.btn-brand.focus, a.btn.btn-brand:focus,
.btn.btn-brand.focus,
.btn.btn-brand:focus {
  background: #ceb66f;
  color: #FFF;
}

a.btn.btn-brand:hover,
.btn.btn-brand:hover {
  background: #ceb66f;
}

a.btn.btn-brand[disabled],
.btn.btn-brand[disabled] {
  background: #c7ac5c;
}

.hide {
  display: none;
}

strong small {
  font-weight: bold;
}

b,
strong {
  font-weight: bold;
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Montserrat";
}

textarea {
  resize: none;
}

.topper {
  transition: all 0.5s;
  position: fixed;
  bottom: 15px;
  bottom: -60px;
  right: 15px;
  z-index: 300;
}

.topper i.fa {
  background: #b3953d;
  color: #FFF;
  padding: 20px;
  font-size: 20px;
  border-radius: 32px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
}

/* FAVORITOS */
.section-favs {
  z-index: 999;
  position: fixed;
  top: 60%;
  right: -220px;
  width: 250px;
  height: 485px;
  height: 530px;
  overflow: hidden;
  margin-top: -240px;
  display: none;
}

.section-favs h5 {
  color: #b3953d;
  font: 700 18px/18px "Montserrat", Helvetica, sans-serif;
  text-align: left;
  padding: 0 10px 0;
  margin: 10px 0 0;
}

.section-favs .btn-favs {
  color: #fff;
  font: 400 14px/14px "Montserrat", Helvetica, sans-serif;
  text-align: center;
  width: 200px;
  margin: 10px 10px 0 10px;
  padding: 5px;
  text-transform: capitalize;
}

.section-favs .fav-lista {
  position: relative;
  top: -8px;
  top: -30px;
  right: -29px;
  z-index: 3;
  height: 490px;
  background: #ebebeb;
  overflow: hidden;
  border: 1px solid #ccc;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
}

.section-favs .fav-lista ul.fav-itens {
  padding: 0 0 5px;
  overflow-y: hidden;
  overflow-x: hidden;
}

.section-favs .fav-lista ul.fav-itens li.item {
  background: #fff;
  box-shadow: 1px 1px 1px 0 rgba(0, 0, 0, 0.5);
  padding: 5px 15px;
  list-style: none;
  display: inline-block;
  width: 200px;
  margin: 10px 10px 0;
}

.section-favs .fav-lista ul.fav-itens li.item h6 {
  margin-top: 8px;
  margin-bottom: 15px;
  font: 500 14px/14px "Montserrat", Helvetica, sans-serif;
  position: relative;
}

.section-favs .fav-lista ul.fav-itens li.item h6.preco {
  font-size: 12px;
  margin-bottom: 10px;
  min-height: 15px;
}

.section-favs .fav-lista ul.fav-itens li.item h6.preco strong {
  font-size: 16px;
  display: block;
  font-weight: bold;
}

.section-favs .fav-lista ul.fav-itens li.item h6 .value {
  font: 700 18px/18px "Montserrat", Helvetica, sans-serif;
}

.section-favs .fav-lista ul.fav-itens li.item h6 .remove-fav {
  color: #c00;
  cursor: pointer;
  font-size: 14px;
  position: absolute;
  right: 0;
  top: 10px;
}

.section-favs .fav-lista ul.fav-itens li.item .foto {
  width: 100%;
}

.section-favs .fav-lista ul.fav-itens li.item .foto img {
  width: 100%;
}

.section-favs .fav-lista ul.fav-itens li.item .caracteristicas {
  padding: 0;
  min-height: 20px;
  float: left;
  text-align: center;
  width: 100%;
}

.section-favs .fav-lista ul.fav-itens li.item .caracteristicas li {
  list-style: none;
  display: inline-block;
}

.section-favs .fav-lista ul.fav-itens li.item.empreendimentos .caracteristicas li {
  display: block;
  text-align: left;
  line-height: 24px;
}

.bt-favo {
  cursor: pointer;
  color: #bb0c33;
  background: #ebebeb;
  margin: 0;
  display: block;
  position: relative;
  top: 215px;
  right: 60px;
  font-size: 18px;
  width: 150px;
  text-align: center;
  height: 30px;
  padding: 2px 0 0;
  z-index: 4;
  border: 1px solid #ccc;
  border-top: 0;
  -webkit-transform: rotate(90deg);
  /* Safari */
  -moz-transform: rotate(90deg);
  /* Firefox */
  -ms-transform: rotate(90deg);
  /* IE */
  -o-transform: rotate(90deg);
  /* Opera */
}

.bt-favo span {
  font: 200 14px "Montserrat", Helvetica, sans-serif;
  color: #b3953d;
}

.icon-favoritos.fav-interna {
  z-index: 1005;
  position: relative;
}

.icon-favoritos .btn-favoritos {
  background: transparent;
  position: absolute;
  z-index: 20;
  font-size: 22px;
  padding: 0;
  margin: 13px;
  color: #FFF;
  cursor: pointer;
  text-decoration: none;
}

.icon-favoritos .btn-favoritos span {
  display: none;
}

.icon-favoritos .btn-favoritos .fa {
  padding: 2px;
  top: 0;
  position: absolute;
  left: 0;
  color: #fff;
}

.icon-favoritos .btn-favoritos .animated {
  /*transition: all 0.5s ease-in-out;*/
}

.icon-favoritos .btn-favoritos.favoritado .fa-heart {
  background: #e00;
  background: linear-gradient(#e00, #900);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.chk-label {
  padding: 6px;
  margin: 0;
  text-transform: uppercase;
  cursor: pointer;
}

.form-contato input[type="checkbox"] {
  margin-right: 5px;
}

.full-loader {
  display: none;
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background: #fff;
  z-index: 9999;
  text-align: center;
  padding-top: 20%;
  opacity: 0.7;
}

