  font-weight: 900;
  color: #b3953d;
  margin-right: 7px;
  width: 15px;
  height: 15px;
  display: inline-block;
}

.page .nice-select .option.selected {
  content: "\f00c";
}

.page .nice-select .option:hover {
  background: #b3953d !important;
  color: #fff;
}

.page .nice-select .option:hover:before {
  content: "\f00c";
  color: #fff;
}

.page .nice-select .option.focus:before {
