// Vitrines
.vitrine {
    padding: 50px 0;
  
    h2 {
      text-align: left;
      color: $color-base-darker;
      font-weight: 700;
      font-size: 26px;
      margin-bottom: 30px;
    }
  
    .subtitulo {
      text-align: left;
    }
  
    .product_itemBx {
      width: 261px;
      height: 380px;
      background: #ffffff 0% 0% no-repeat padding-box;
      border: 1px solid #c4c4c4;
      border-radius: 20px;
      opacity: 1;
      @media screen and (min-width:300px) and (max-width:720px) {
        width: 90%;
        margin: 0 auto; 
      }
  
      .statustag {
        background: rgba(0, 0, 0, 0.65);
        position: absolute;
        right: 10px;
        top: 10px;
        padding: 3px 10px;
        color: #fff;
        border-radius: 4px;
        z-index: 9;
      }
  
      .product_itemBxTop {
        position: relative;
  
        .vit_link_foto {
          display: block;
  
          &:hover {
            .hover_img {
              visibility: visible;
              opacity: 1;
            }
          }
  
          .vitrine-venda-img {
            width: 261px;
            height: 170px !important;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center;
            border-radius: 10px 10px 0px 0px;
            @media screen and (min-width:300px) and (max-width:720px) {
              width: 100%;
            }
          }
  
          .hover_img {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            background: rgba(0, 0, 0, 0.5);
            visibility: hidden;
            opacity: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            font-family: $font-icon-free;
            font-weight: 900;
            color: #fff;
            font-size: 24px;
            transition: all 0.3s;
            z-index: 5;
            border-radius: 10px;
  
            &:after {
              content: "\f002";
            }
          }
        }
      }
  
      .product_itemBxBot {
        position: relative;
        padding-bottom: 70px;
        min-height: 200px;
        
        .content_imv_caract {
          display: flex;
          flex-wrap: wrap;

          .item {

            &.wt-100 {
              width: 100%;
            }

            &.wt-50 {
              width: 50%;
            }

            display: flex;
            margin-top: 2%;
            img {
              width: 10%;
              margin-right: 7%;
            }

            i {
              margin-right: 10%;
            }
          }
        }

        .valor {
          margin-top: 6%;
          span {
            font-size: 13px;
            font-weight: 600;
            line-height: 1.3;
          }
        }
        .tipos {
          font-size: 12px;
          margin: 5px 0;
        }
  
        .txt_info {
          font-size: 14px;
        }
  
        h3 {
          font-weight: bold;
          font-size: 14px;
          color: $dark-color;
          margin-bottom: 10px;
          display: flex;
          align-items: center;
          justify-content: left;
          font-family: Montserrat;
        }
  
        h5 {
          font-size: 14px;
          font-weight: normal;
        }
  
        .valor {
          text-align: center;
        }
  
        sup {
          font-size: 10px;
        }
  
        .wrapper_btn_more {
          position: absolute;
          width: 100%;
          left: 0;
          bottom: 15px;
  
          .bt-saibamais {
            background: $color-base;
            color: #fff;
            padding: 8px 15px;
            display: inline-block;
            vertical-align: top;
            width: 85%;
            border-radius: 4px;
          }
        }
      }
    }
  
    .owl-nav {
      display: none;
    }
  
    p.text-left {
      font-size: 14px;
  }}
  
  #home-vitrine-regioes {
    // background: radial-gradient(circle at 0% 50%, rgba(96, 16, 48, 0) 9px, #f1f1f1 10px, rgba(96, 16, 48, 0) 11px) 0px 10px,
    //             radial-gradient(at 100% 100%, rgba(96, 16, 48, 0) 9px, #f1f1f1 10px, rgba(96, 16, 48, 0) 11px), #fff;
    // background-size: 20px 20px;
    box-shadow: inset 0px -5px 10px rgba(0, 0, 0, 0.05),
      inset 0px 5px 10px rgba(0, 0, 0, 0.1);
    border-bottom: 1px solid #f1f1f1;
    background: #f1f1f1;
  
    .box_regiao {
      display: block;
  
      p {
        margin-bottom: 0;
      }
  
      &:hover {
        text-decoration: none;
  
        .container-regiao {
          &:before {
            top: 0;
          }
  
          h4 {
            margin-bottom: 10px;
          }
  
          i {
            margin-left: 15px;
          }
        }
      }
  
      .container-regiao {
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        color: #fff;
        min-height: 180px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        padding: 20px;
        margin-bottom: 20px;
        position: relative;
  
        * {
          position: relative;
          z-index: 9;
          transition: all 0.3s;
        }
  
        &:before {
          content: "";
          position: absolute;
          bottom: 0;
          left: 0;
          right: 0;
          top: 25%;
          z-index: 1;
          transition: all 0.3s;
          background: rgba(0, 0, 0, 0);
          background: -moz-linear-gradient(
            top,
            rgba(0, 0, 0, 0) 0%,
            rgba(0, 0, 0, 0.85) 100%
          );
          background: -webkit-gradient(
            left top,
            left bottom,
            color-stop(0%, rgba(0, 0, 0, 0)),
            color-stop(100%, rgba(0, 0, 0, 0.85))
          );
          background: -webkit-linear-gradient(
            top,
            rgba(0, 0, 0, 0) 0%,
            rgba(0, 0, 0, 0.85) 100%
          );
          background: -o-linear-gradient(
            top,
            rgba(0, 0, 0, 0) 0%,
            rgba(0, 0, 0, 0.85) 100%
          );
          background: -ms-linear-gradient(
            top,
            rgba(0, 0, 0, 0) 0%,
            rgba(0, 0, 0, 0.85) 100%
          );
          background: linear-gradient(
            to bottom,
            rgba(0, 0, 0, 0) 0%,
            rgba(0, 0, 0, 0.85) 100%
          );
          filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#000000', endColorstr='#000000', GradientType=0 );
        }
  
        i {
          margin-left: 10px;
          font-size: 12px;
        }
      }
    }
  }
  
  @media screen and (min-width: 576px) and (max-width: 769px) {
    .vitrine {
      .product_itemBx {
        .product_itemBxBot {
          h3 {
            min-height: 45px;
          }
        }
      }
    }
  }
  
  @media screen and (min-width: 769px) {
    .vitrine {
      .product_itemBx {
        .product_itemBxBot {
          min-height: 315px;
  
          h3 {
            font-size: 14px;
            min-height: 20px;
          }
  
          .valor {
            strong {
              font-size: 22px;
            }
          }
        }
      }
  
      .owl-nav {
        display: block;
        position: absolute;
        z-index: 1010;
        width: 100%;
        height: 0;
        top: 50%;
  
        .owl-prev,
        .owl-next {
          display: inline-block;
          position: absolute;
          top: -20px;
          transition: all 0.3s;
  
          i {
            width: 40px;
            height: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: #333;
            font-size: 24px;
          }
  
          &.disabled {
            opacity: 0.4;
          }
  
          &:not(.disabled):hover {
            i {
              color: $color-base;
            }
          }
        }
  
        .owl-prev {
          left: -50px;
        }
  
        .owl-next {
          right: -50px;
        }
      }
  
      &.emp_vitrine {
        .product_itemBx {
          .product_itemBxBot {
            min-height: 265px;
          }
        }
      }
  
      &#vitrine-ultimosacessados {
        .product_itemBx {
          .product_itemBxBot {
            min-height: 215px;
          }
        }
  
        .owl-carousel .owl-stage-outer {
          padding-bottom: 20px;
        }
      }
    }
  }
  

  //

  .vitrine .product_itemBx .product_itemBxBot .valor, .vitrine.emp_vitrine .product_itemBx .product_itemBxBot h3 {
  
    color: $color-base;;
}

